function array_js() {
    //objects
    //task_1

    /*–°–æ–∑–¥–∞—Ç—å –º–∞—Å—Å–∏–≤ ¬´–°–ø–∏—Å–æ–∫ –ø–æ–∫—É–ø–æ–∫¬ª. –ö–∞–∂–¥—ã–π —ç–ª–µ–º–µ–Ω—Ç –º–∞—Å—Å–∏–≤–∞ —è–≤–ª—è–µ—Ç—Å—è –æ–±—ä–µ–∫—Ç–æ–º, 
    –∫–æ—Ç–æ—Ä—ã–π —Å–æ–¥–µ—Ä–∂–∏—Ç –Ω–∞–∑–≤–∞–Ω–∏–µ –ø—Ä–æ–¥—É–∫—Ç–∞, –Ω–µ–æ–±—Ö–æ–¥–∏–º–æ–µ –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ –∏ –∫—É–ø–ª–µ–Ω –∏–ª–∏ –Ω–µ—Ç. 
    –ù–∞–ø–∏—Å–∞—Ç—å –Ω–µ—Å–∫–æ–ª—å–∫–æ —Ñ—É–Ω–∫—Ü–∏–π –¥–ª—è —Ä–∞–±–æ—Ç—ã —Å —Ç–∞–∫–∏–º –º–∞—Å—Å–∏–≤–æ–º.
        –í—ã–≤–æ–¥ –≤—Å–µ–≥–æ —Å–ø–∏—Å–∫–∞ –Ω–∞ —ç–∫—Ä–∞–Ω —Ç–∞–∫–∏–º –æ–±—Ä–∞–∑–æ–º, —á—Ç–æ–±—ã —Å–Ω–∞—á–∞–ª–∞ —à–ª–∏ –Ω–µ–∫—É–ø–ª–µ–Ω–Ω—ã–µ –ø—Ä–æ–¥—É–∫—Ç—ã, 
        –∞ –ø–æ—Ç–æ–º ‚Äì –∫—É–ø–ª–µ–Ω–Ω—ã–µ.
        –î–æ–±–∞–≤–ª–µ–Ω–∏–µ –ø–æ–∫—É–ø–∫–∏ –≤ —Å–ø–∏—Å–æ–∫. –£—á—Ç–∏—Ç–µ, —á—Ç–æ –ø—Ä–∏ –¥–æ–±–∞–≤–ª–µ–Ω–∏–∏ –ø–æ–∫—É–ø–∫–∏ —Å —É–∂–µ —Å—É—â–µ—Å—Ç–≤—É—é—â–∏–º 
        –≤ —Å–ø–∏—Å–∫–µ –ø—Ä–æ–¥—É–∫—Ç–æ–º, –Ω–µ–æ–±—Ö–æ–¥–∏–º–æ —É–≤–µ–ª–∏—á–∏–≤–∞—Ç—å –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ –≤ —Å—É—â–µ—Å—Ç–≤—É—é—â–µ–π –ø–æ–∫—É–ø–∫–µ, –∞ –Ω–µ –¥–æ–±–∞–≤–ª—è—Ç—å –Ω–æ–≤—É—é.
        –ü–æ–∫—É–ø–∫–∞ –ø—Ä–æ–¥—É–∫—Ç–∞. –§—É–Ω–∫—Ü–∏—è –ø—Ä–∏–Ω–∏–º–∞–µ—Ç –Ω–∞–∑–≤–∞–Ω–∏–µ –ø—Ä–æ–¥—É–∫—Ç–∞ –∏ –æ—Ç–º–µ—á–∞–µ—Ç –µ–≥–æ –∫–∞–∫ –∫—É–ø–ª–µ–Ω–Ω—ã–π.*/

    $("#g6t1").on("click", function(){
        class Product {
            constructor(params = {}) {
              Object.assign(this, {
                pName: null, 
                amount: 1, 
                bought: false
              }, { ...params }); 
            }
          
            static sortDefault(a, b) {  // —á—Ç–æ–±—ã —Å–Ω–∞—á–∞–ª–∞ —à–ª–∏ –Ω–µ–∫—É–ø–ª–µ–Ω–Ω—ã–µ –ø—Ä–æ–¥—É–∫—Ç—ã, –∞ –ø–æ—Ç–æ–º ‚Äì –∫—É–ø–ª–µ–Ω–Ω—ã–µ
              return (a.bought - b.bought) * 10 + a.pName.localeCompare(b.pName); 
            }; 
          }
          Product.prototype.toString = function () {
            return `${this.pName} (x${this.amount}), ${this.bought ? '–∫—É–ø–ª–µ–Ω–æ' : '–Ω–µ –∫—É–ø–ª–µ–Ω–æ'}`;
          }; 
          
          
          const shopList = [
            { pName: '–ë–∞–Ω–∞–Ω', amount: 2 },
            { pName: '–ê–ø–µ–ª—å—Å–∏–Ω', amount: 5, bought: true },
            { pName: '–ú–æ–ª–æ–∫–æ', bought: true },
            { pName: '–ì—Ä—É—à–∞', amount: 10 }
          ].map(prodDef => new Product(prodDef));
          
          const addToShopList = prodDef => {  // –ø—Ä–∏ –¥–æ–±–∞–≤–ª–µ–Ω–∏–∏ –ø–æ–∫—É–ø–∫–∏ —Å —É–∂–µ —Å—É—â–µ—Å—Ç–≤—É—é—â–∏–º –≤ —Å–ø–∏—Å–∫–µ –ø—Ä–æ–¥—É–∫—Ç–æ–º, –Ω–µ–æ–±—Ö–æ–¥–∏–º–æ —É–≤–µ–ª–∏—á–∏–≤–∞—Ç—å –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ –≤ —Å—É—â–µ—Å—Ç–≤—É—é—â–µ–π –ø–æ–∫—É–ø–∫–µ, –∞ –Ω–µ –¥–æ–±–∞–≤–ª—è—Ç—å –Ω–æ–≤—É—é
            const prod = shopList.find(prod => prod.pName === prodDef.pName); 
            if (!prod) return shopList.push(new Product(prodDef)); 
            prod.amount += prodDef.amount; 
          }; 
          const printShopList = () => shopList.forEach(
            (prod, i) => console.log(`${i + 1}. ${prod}`)
          ); 
          
          shopList.sort(Product.sortDefault);
          printShopList();
          console.log('---'); 
          
          addToShopList({ pName: '–ë–∞–Ω–∞–Ω', amount: 3 });
          addToShopList({ pName: '–®–æ–∫–æ–ª–∞–¥', amount: 2 });
          shopList.sort(Product.sortDefault);
          printShopList();
    });

    //task_2

    /*–°–æ–∑–¥–∞—Ç—å –º–∞—Å—Å–∏–≤, –æ–ø–∏—Å—ã–≤–∞—é—â–∏–π —á–µ–∫ –≤ –º–∞–≥–∞–∑–∏–Ω–µ. 
    –ö–∞–∂–¥—ã–π —ç–ª–µ–º–µ–Ω—Ç –º–∞—Å—Å–∏–≤–∞ —Å–æ—Å—Ç–æ–∏—Ç –∏–∑ –Ω–∞–∑–≤–∞–Ω–∏—è —Ç–æ–≤–∞—Ä–∞, 
    –∫–æ–ª–∏—á–µ—Å—Ç–≤–∞ –∏ —Ü–µ–Ω—ã –∑–∞ –µ–¥–∏–Ω–∏—Ü—É —Ç–æ–≤–∞—Ä–∞. –ù–∞–ø–∏—Å–∞—Ç—å —Å–ª–µ–¥—É—é—â–∏–µ —Ñ—É–Ω–∫—Ü–∏–∏:
    –†–∞—Å–ø–µ—á–∞—Ç–∫–∞ —á–µ–∫–∞ –Ω–∞ —ç–∫—Ä–∞–Ω;
    –ü–æ–¥—Å—á–µ—Ç –æ–±—â–µ–π —Å—É–º–º—ã –ø–æ–∫—É–ø–∫–∏;
    –ü–æ–ª—É—á–µ–Ω–∏–µ —Å–∞–º–æ–π –¥–æ—Ä–æ–≥–æ–π –ø–æ–∫—É–ø–∫–∏ –≤ —á–µ–∫–µ;
    –ü–æ–¥—Å—á–µ—Ç —Å—Ä–µ–¥–Ω–µ–π —Å—Ç–æ–∏–º–æ—Å—Ç–∏ –æ–¥–Ω–æ–≥–æ —Ç–æ–≤–∞—Ä–∞ –≤ —á–µ–∫–µ.*/
    
    $("#g6t2").on("click", function(){
        const checkArr = [
          { 
            name: 'apple',
            count: 2,
            price: 28
          },
          { 
            name: 'kivi',
            count: 1,
            price: 10
          },
          { 
            name: 'banana',
            count: 2,
            price: 30
          }
        ]
        // –†–∞—Å–ø–µ—á–∞—Ç–∫–∞ —á–µ–∫–∞ –Ω–∞ —ç–∫—Ä–∞–Ω;
        checkArr.forEach(check => console.log(check));
        //–ü–æ–¥—Å—á–µ—Ç –æ–±—â–µ–π —Å—É–º–º—ã –ø–æ–∫—É–ø–∫–∏;
        const amount = checkArr.reduce((total, checkArr)=> total + checkArr.price, 0);
        console.log('The amount of your purchase is' + ` ${amount}`);
        //–ü–æ–ª—É—á–µ–Ω–∏–µ —Å–∞–º–æ–π –¥–æ—Ä–æ–≥–æ–π –ø–æ–∫—É–ø–∫–∏ –≤ —á–µ–∫–µ;
        let max = 0;
        checkArr.forEach(checkArr => checkArr.price > max ? max = checkArr.price : checkArr.price);
        console.log ('The most expensive purchase in check is' + ` ${max}`);
        //–ü–æ–¥—Å—á–µ—Ç —Å—Ä–µ–¥–Ω–µ–π —Å—Ç–æ–∏–º–æ—Å—Ç–∏ –æ–¥–Ω–æ–≥–æ —Ç–æ–≤–∞—Ä–∞ –≤ —á–µ–∫–µ.
        const average = amount / checkArr.length;
        console.log ('The average purchase price in the check' + ` ${average.toFixed(1)}`);
    });
    //task_3

    /*–°–æ–∑–¥–∞—Ç—å –æ–±—ä–µ–∫—Ç, –æ–ø–∏—Å—ã–≤–∞—é—â–∏–π –≤—Ä–µ–º—è (—á–∞—Å—ã, –º–∏–Ω—É—Ç—ã, —Å–µ–∫—É–Ω–¥—ã), 
    –∏ —Å–ª–µ–¥—É—é—â–∏–µ —Ñ—É–Ω–∫—Ü–∏–∏ –¥–ª—è —Ä–∞–±–æ—Ç—ã —Å —ç—Ç–∏–º –æ–±—ä–µ–∫—Ç–æ–º: 
    –§—É–Ω–∫—Ü–∏—è –≤—ã–≤–æ–¥–∞ –≤—Ä–µ–º–µ–Ω–∏ –Ω–∞ —ç–∫—Ä–∞–Ω;
    –§—É–Ω–∫—Ü–∏—è –∏–∑–º–µ–Ω–µ–Ω–∏—è –≤—Ä–µ–º–µ–Ω–∏ –Ω–∞ –ø–µ—Ä–µ–¥–∞–Ω–Ω–æ–µ –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ —Å–µ–∫—É–Ω–¥;
    –§—É–Ω–∫—Ü–∏—è –∏–∑–º–µ–Ω–µ–Ω–∏—è –≤—Ä–µ–º–µ–Ω–∏ –Ω–∞ –ø–µ—Ä–µ–¥–∞–Ω–Ω–æ–µ –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ –º–∏–Ω—É—Ç;
    –§—É–Ω–∫—Ü–∏—è –∏–∑–º–µ–Ω–µ–Ω–∏—è –≤—Ä–µ–º–µ–Ω–∏ –Ω–∞ –ø–µ—Ä–µ–¥–∞–Ω–Ω–æ–µ –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ —á–∞—Å–æ–≤. 
    –£—á—Ç–∏—Ç–µ, —á—Ç–æ –≤ –ø–æ—Å–ª–µ–¥–Ω–∏—Ö 3-—Ö —Ñ—É–Ω–∫—Ü–∏—è—Ö, –ø—Ä–∏ –∏–∑–º–µ–Ω–µ–Ω–∏–∏ –æ–¥–Ω–æ–π —á–∞—Å—Ç–∏ –≤—Ä–µ–º–µ–Ω–∏,
    –º–æ–∂–µ—Ç –∏–∑–º–µ–Ω–∏—Ç—å—Å—è –∏ –¥—Ä—É–≥–∞—è. –ù–∞–ø—Ä–∏–º–µ—Ä, –µ—Å–ª–∏ –∫–æ –≤—Ä–µ–º–µ–Ω–∏ ¬´20:30:45¬ª –¥–æ–±–∞–≤–∏—Ç—å 30 —Å–µ–∫—É–Ω–¥, 
    —Ç–æ –¥–æ–ª–∂–Ω–æ –ø–æ–ª—É—á–∏—Ç—å—Å—è ¬´20:31:15¬ª, –∞ –Ω–µ ¬´20:30:75¬ª. */

    $("#g6t3").on("click", function(){
      let s = {
        color: "red",
        border: "1px solid black",
        font: "50px",
        align: "center"

      }
      let genStyle = genStrStyleFromObj(s);
      function genStrStyleFromObj(obj){
          let strStyle = "";
          for (const[attrStyle, valStyle] of Object.entries(obj)){
              strStyle += `${attrStyle}: ${valStyle};`;
          }
          return strStyle;
      }
      let p = document.createElement("p");
      p.style.cssText = genStyle
      p.style.fontSize = s.font
      p.style.textAlign = s.align
      p.innerHTML = "Hello world"
      document.write(p.outerHTML)  
  });

  //task_4

    /*–°–æ–∑–¥–∞—Ç—å –º–∞—Å—Å–∏–≤ –∞—É–¥–∏—Ç–æ—Ä–∏–π –∞–∫–∞–¥–µ–º–∏–∏. –û–±—ä–µ–∫—Ç-–∞—É–¥–∏—Ç–æ—Ä–∏—è —Å–æ—Å—Ç–æ–∏—Ç –∏–∑ –Ω–∞–∑–≤–∞–Ω–∏—è, 
    –∫–æ–ª–∏—á–µ—Å—Ç–≤–∞ –ø–æ—Å–∞–¥–æ—á–Ω—ã—Ö –º–µ—Å—Ç (–æ—Ç 10 –¥–æ 20) –∏ –Ω–∞–∑–≤–∞–Ω–∏—è —Ñ–∞–∫—É–ª—å—Ç–µ—Ç–∞, 
    –¥–ª—è –∫–æ—Ç–æ—Ä–æ–≥–æ –æ–Ω–∞ –ø—Ä–µ–¥–Ω–∞–∑–Ω–∞—á–µ–Ω–∞. –ù–∞–ø–∏—Å–∞—Ç—å –Ω–µ—Å–∫–æ–ª—å–∫–æ —Ñ—É–Ω–∫—Ü–∏–π –¥–ª—è —Ä–∞–±–æ—Ç—ã —Å –Ω–∏–º
    –í—ã–≤–æ–¥ –Ω–∞ —ç–∫—Ä–∞–Ω –≤—Å–µ—Ö –∞—É–¥–∏—Ç–æ—Ä–∏–π;
    –í—ã–≤–æ–¥ –Ω–∞ —ç–∫—Ä–∞–Ω –∞—É–¥–∏—Ç–æ—Ä–∏–π –¥–ª—è —É–∫–∞–∑–∞–Ω–Ω–æ–≥–æ —Ñ–∞–∫—É–ª—å—Ç–µ—Ç–∞;
    –í—ã–≤–æ–¥ –Ω–∞ —ç–∫—Ä–∞–Ω —Ç–æ–ª—å–∫–æ —Ç–µ—Ö –∞—É–¥–∏—Ç–æ—Ä–∏–π, –∫–æ—Ç–æ—Ä—ã–µ –ø–æ–¥—Ö–æ–¥—è—Ç –¥–ª—è –ø–µ—Ä–µ–¥–∞–Ω–Ω–æ–π –≥—Ä—É–ø–ø—ã. –û–±—ä–µ–∫—Ç-–≥—Ä—É–ø–ø–∞ —Å–æ—Å—Ç–æ–∏—Ç –∏–∑ –Ω–∞–∑–≤–∞–Ω–∏—è, –∫–æ–ª–∏—á–µ—Å—Ç–≤–∞ —Å—Ç—É–¥–µ–Ω—Ç–æ–≤ –∏ –Ω–∞–∑–≤–∞–Ω–∏—è —Ñ–∞–∫—É–ª—å—Ç–µ—Ç–∞;
    –§—É–Ω–∫—Ü–∏—è —Å–æ—Ä—Ç–∏—Ä–æ–≤–∫–∏ –∞—É–¥–∏—Ç–æ—Ä–∏–π –ø–æ –∫–æ–ª–∏—á–µ—Å—Ç–≤—É –º–µ—Å—Ç;
    –§—É–Ω–∫—Ü–∏—è —Å–æ—Ä—Ç–∏—Ä–æ–≤–∫–∏ –∞—É–¥–∏—Ç–æ—Ä–∏–π –ø–æ –Ω–∞–∑–≤–∞–Ω–∏—é (–ø–æ –∞–ª—Ñ–∞–≤–∏—Ç—É).*/

    $("#g6t4").on("click", function(){
      const audienceArr = [
        { audienceName: 'mif_1', numSets: 10, facultyName: 'mif' },
        { audienceName: 'fiz_2', numSets: 20, facultyName: 'fiz' },
        { audienceName: 'mif_2', numSets: 15, facultyName: 'mif'  },
        { audienceName: 'mif_3', numSets: 16, facultyName: 'mif' },
        { audienceName: 'fiz_1', numSets: 10, facultyName: 'fiz' },
        { audienceName: 'ped_1', numSets: 12, facultyName: 'ped' },
        { audienceName: 'ped_2', numSets: 15, facultyName: 'ped' }
      ]
      //  –í—ã–≤–æ–¥ –Ω–∞ —ç–∫—Ä–∞–Ω –≤—Å–µ—Ö –∞—É–¥–∏—Ç–æ—Ä–∏–π;
      audienceArr.forEach(audience => console.log(audience));

      //–í—ã–≤–æ–¥ –Ω–∞ —ç–∫—Ä–∞–Ω –∞—É–¥–∏—Ç–æ—Ä–∏–π –¥–ª—è —É–∫–∞–∑–∞–Ω–Ω–æ–≥–æ —Ñ–∞–∫—É–ª—å—Ç–µ—Ç–∞;
      const mif_1 = audienceArr.filter(audience => audience.facultyName ==='mif');
      console.log(mif_1);

      //–í—ã–≤–æ–¥ –Ω–∞ —ç–∫—Ä–∞–Ω —Ç–æ–ª—å–∫–æ —Ç–µ—Ö –∞—É–¥–∏—Ç–æ—Ä–∏–π, –∫–æ—Ç–æ—Ä—ã–µ –ø–æ–¥—Ö–æ–¥—è—Ç –¥–ª—è –ø–µ—Ä–µ–¥–∞–Ω–Ω–æ–π –≥—Ä—É–ø–ø—ã. 
      //–û–±—ä–µ–∫—Ç-–≥—Ä—É–ø–ø–∞ —Å–æ—Å—Ç–æ–∏—Ç –∏–∑ –Ω–∞–∑–≤–∞–Ω–∏—è, –∫–æ–ª–∏—á–µ—Å—Ç–≤–∞ —Å—Ç—É–¥–µ–Ω—Ç–æ–≤ –∏ –Ω–∞–∑–≤–∞–Ω–∏—è —Ñ–∞–∫—É–ª—å—Ç–µ—Ç–∞;
      const group = {
        name: 'mif22',
        quantity: 15,
        fName: 'mif'
      }
      const audience = audienceArr.filter(audience => group.quantity <= audience.numSets && group.fName === audience.facultyName)
      console.log(group, audience)

      //–§—É–Ω–∫—Ü–∏—è —Å–æ—Ä—Ç–∏—Ä–æ–≤–∫–∏ –∞—É–¥–∏—Ç–æ—Ä–∏–π –ø–æ –∫–æ–ª–∏—á–µ—Å—Ç–≤—É –º–µ—Å—Ç;
      
      audienceArr.sort(function (a, b) {
        return a.numSets - b.numSets;
      });
      console.log(audienceArr)

      //–§—É–Ω–∫—Ü–∏—è —Å–æ—Ä—Ç–∏—Ä–æ–≤–∫–∏ –∞—É–¥–∏—Ç–æ—Ä–∏–π –ø–æ –Ω–∞–∑–≤–∞–Ω–∏—é (–ø–æ –∞–ª—Ñ–∞–≤–∏—Ç—É).

      audienceArr.sort(function(a, b) {
        var audienceNameA = a.audienceName.toUpperCase(); 
        var audienceNameB = b.audienceName.toUpperCase(); 
        if (audienceNameA < audienceNameB) {
          return -1;
        }
        if (audienceNameA > audienceNameB) {
          return 1;
        }
        return 0;
      });
      console.log(audienceArr)
      
  });

}
export {array_js};